import myProject from "../../../components/myProject.vue";
// import ff from "../../../common/com.js";
// var oUseTool = new oFuncs();

export default {
	data() {
		return {
			actlist: []
		}
	},
	components:{
		myProject
	},
	onLoad: function() {
		uni.request({
			url: this.api + '/basicdata/getmyclass.php',
			data: {
				flag:'classMes',
			},
			method: 'POST',
			dataType: 'json',
			header: {
				'content-type': 'application/x-www-form-urlencoded', //POST头信息
			},
			success: (res) => {
				console.log(res.data);
				res=res.data;
				if(res[res.length-1].result=='success'){
					res.splice(res.length-1,1);
					this.actlist=res;
				}else{
					uni.showToast({
						icon: 'error',
						position: 'bottom',
						title: '无数据'
					});
				}
			},
			fail: (error) => {
				console.log(error);
			}
		});
	},
	methods: {
		options(){
				  //  uni.showToast({
				  //      icon: 'none',
				  //      title: '账号最短为 5 个字符'
				  //  });
				  uni.navigateTo({
				  	url: '../../my_project/department/department',
				  });
		}
	},
}
